
import Card from 'vux/src/components/card/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import { mapState } from 'vuex'
import { cloneObj } from '@/utils/utils.js'
import { setTimeout } from 'timers'
export default {
components: {
Card,
Scroller
},
activated() {
this.query()
this.util.unmask()
},
computed: {
...mapState({
userUid: state => state.userUid,
userName: state => state.userName
})
},
created() {

},
methods: {
refresh() {
this.query('refresh')
this.$nextTick(() => {
setTimeout(() => {
this.$refs.scroller.donePulldown()
this.pullupEnabled && this.$refs.scroller.enablePullup()
}, 0)
})
},
isObject(dest, arr) {
var map = {}
for (let i = 0; i < dest.length; i++) {
var ai = dest[i]

if (!map[ai.AddName_CN]) {
arr.push({
AddName_CN: ai.AddName_CN,
data: [ai]
})
map[ai.AddName_CN] = ai
} else {
for (let k = 0; k < arr.length; k++) {
const dj = arr[k]
if (dj.AddName_CN == ai.AddName_CN) {
dj.data.push(ai)
break
}
}
}
}
},
query() {
this.arrOri = []
var _this = this,
url =
'/admin/appAgentAction!runAgent.action?agentId=Agent_Wechat_exercise&appId=WeChat&Type=getschedule',
params = {}
this.util.post(url, params, function(res) {
if (res.status) {
_this.isObject(res.data.data, _this.arrOri)
} else {
return _this.util.alert(res.msg)
}
})
},
particulars(item) {

this.$router.push({
name: 'details',
query: {
pType: 'detail',
data: item
},
params: { data: item }
})
},
changePullupStatus(enabled) {
this.$refs.scroller.donePullup()
if (enabled) {

this.pullupEnabled = true
} else {
this.$refs.scroller.disablePullup()
this.pullupEnabled = false
}
}
},
data() {
return {
isEnd: false,
arrOri: [],
departId: '',
status: {
pulldownStatus: 'default'
},
pulldown: {
content: '下拉刷新',
height: 60,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '释放刷新',
loadingContent: '刷新中...'

},
page: 1,
pageSize: 10
}
}
}



